// import { UserRoleService } from './user-role.service';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';

// import { JwtHelperService } from '@auth0/angular-jwt';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root',
})
export class AuthService {
  private apiPath: string = environment.apiPath;

  constructor(
    private http: HttpClient,
    private router: Router // private tokenVerify: JwtHelperService, // private roleService: UserRoleService
  ) {}

  public login(body) {
    return this.http.post<any>(this.apiPath + '/users/login', body);
  }

  public logout() {
    this.clearToken();
    this.router.navigateByUrl('/login');
  }

  public getToken() {
    try {
      return localStorage.getItem('token');
    } catch (ex) {
      console.log(ex);
    }
  }

  public getDecodedToken() {
    try {
      const token = localStorage.getItem('token');
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      var jsonPayload = decodeURIComponent(
        atob(base64)
          .split('')
          .map(function (c) {
            return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join('')
      );
      return JSON.parse(jsonPayload);
    } catch (err) {
      return null;
    }
  }

  public saveToken(token) {
    localStorage.setItem('token', token);
  }

  public clearToken() {
    localStorage.removeItem('token');
    localStorage.removeItem('roles');
    // this.roleService.resetMenuList();
  }

  public saveMenu(menu) {
    localStorage.setItem('roles', JSON.stringify(menu));
  }

  public clearMenu() {
    localStorage.removeItem('roles');
  }

  public getMenu() {
    try {
      return JSON.parse(localStorage.getItem('roles'));
    } catch (ex) {
      console.log(ex);
    }
  }

  public tokenVerifier(token) {
    // return this.tokenVerify.isTokenExpired(token);
  }
}
